@charset "UTF-8";
/*Обнуление*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/*Шрифты*/
@font-face {
  font-family: "Raleway-Bold";
  src: url("../famile/Raleway-Bold.woff2");
}
@font-face {
  font-family: "Raleway-Regular";
  src: url("../famile/Raleway-Regular.woff2");
}
@font-face {
  font-family: "Monoton-Regular";
  src: url("../famile/Monoton-Regular.woff2");
}
@font-face {
  font-family: "Raleway-Medium";
  src: url("../famile/Raleway-Medium.woff2");
}
@font-face {
  font-family: "Raleway-SemiBold";
  src: url("../famile/Raleway-SemiBold.woff2");
}
:root {
  --black: #000000;
  --purple: #aa15b6;
}

html,
body {
  background-color: var(--black);
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  box-sizing: border-box;
}

.container-xxl {
  max-width: 1080px;
  width: 100%;
  margin: 0 auto;
}

.container-1440 {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}

.container {
  max-width: 885px;
  width: 100%;
  margin: 0 auto;
}

::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
  background-color: var(--black);
}

::-webkit-scrollbar-thumb {
  background-color: rgb(18, 18, 18);
}

/* 		HEADER 		 */
.header {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh;
  max-height: 1000px;
  border: 0px solid var(--black);
  overflow: hidden;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}

.header__content {
  background: url("../img/background_header_decorations.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  position: relative;
  padding-top: 1.5rem;
}

a {
  text-decoration: none;
  color: #fff;
}

.nav > ul {
  font-family: "Raleway-Bold";
  font-size: 30px;
  display: flex;
  width: 100%;
  justify-content: flex-end;
  position: relative;
  padding: 0 1rem;
}

.nav li {
  margin-right: 0.5rem;
}

.nav a {
  color: #fff;
  text-decoration: none;
}

.nav a:is(:hover, :focus) {
  transition: all 0.3s ease 0s;
  color: var(--purple);
}

.li_home {
  position: absolute;
  left: 1rem;
}

.menu {
  width: 18px;
  height: 1.5rem;
  position: relative;
  align-items: center;
  margin: auto 0;
  display: flex;
  flex-flow: row wrap;
  z-index: 999;
}

.menu_nav {
  width: 18px;
  height: 1.5rem;
  position: relative;
  align-items: center;
  margin: auto 0;
  display: flex;
  flex-flow: row wrap;
}

.srick {
  display: inline-block;
  width: 100%;
  height: 4px;
  background-color: #fff;
}

.header__text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header__left {
  text-align: center;
  background-color: rgba(255, 0, 81, 0.6);
  border-radius: 50%;
  width: 24rem;
  height: 24rem;
  top: -4%;
  left: 4%;
  position: relative;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  align-content: center;
  text-transform: uppercase;
}

.header__left h1 {
  font-family: "Monoton-Regular";
  font-size: 3.7rem;
  font-weight: 400;
}

.header__left h3 {
  font-family: "Raleway-Medium";
  font-size: 2.5rem;
  line-height: 20px;
  font-weight: 400;
}

.header__right {
  display: inline-block;
  height: 100%;
  width: 50%;
  text-align: end;
  display: flex;
  justify-content: flex-end;
  flex-flow: row wrap;
  align-content: center;
  padding-right: 1rem;
}

.header__right h2 {
  text-transform: uppercase;
  font-size: 3rem;
  font-family: "Raleway-SemiBold";
}

.header__right p {
  font-size: 0.85rem;
  font-family: "Raleway-Medium";
  font-weight: 400;
  width: 80%;
  display: flex;
  justify-content: flex-end;
  flex-flow: row wrap;
  align-content: center;
}

.header__right p::before {
  content: "";
  width: 32%;
  height: 5px;
  background-color: #fff;
  margin-bottom: 15px;
}

.header__right a {
  text-decoration: none;
  font-size: 1rem;
  font-family: "Raleway-Medium";
  color: #100667;
  font-weight: 400;
  padding: 4px 60px;
  border: 0 solid transparent;
  margin-top: 2rem;
  background-color: #fff;
  position: relative;
}

.header__right a:is(:hover, :focus) {
  transition: all 0.3s ease 0s;
  background-color: var(--purple);
}

