}

.footer__date__text ul {
  display: flex;
  justify-content: space-between;
}

.footer__date__text li {
  list-style: none;
  position: relative;
}

.footer__date__text li::before {
  content: "";
  height: 120%;
  width: 4px;
  background-color: #fff;
  position: absolute;
  margin-left: -15px;
}

.footer__date__text h5 {
  font-family: "Raleway-Bold";
  font-size: 0.7rem;
  margin: 0 5px;
}

.footer__date__text h6 {
