  font-size: 0.65rem;
  margin-left: 5px;
}

.footer__quote_2 {
  background-color: var(--black);
  padding: 3vh 1rem;
}

.footer__quote_2__text {
  text-align: center;
}

.footer__quote_2__text h4 {
  font-family: Raleway-Bold;
  font-size: 4rem;
  margin-bottom: -40px;
}

.footer__quote_2__text p {
  font-family: Raleway-Regular;
  font-size: 0.75rem;
  max-width: 550px;
  margin: 0 auto;
  margin-bottom: 2vh;
}

.footer__quote_2__text h6 {
  font-family: Raleway-SemiBold;
  font-size: 0.85rem;
  margin-bottom: 3vh;
}

.footer__quote_2__text .icon {
  display: flex;
  justify-content: center;
}

.footer__quote_2__text img {
  width: 35px;
}

.footer__quote_2__text a {
  margin: 0 1%;
}

.footer__about {
  background-color: var(--purple);
  padding: 8vh 1rem;
}

.footer__about a {
  font-size: 1.5rem;
  font-family: sans-serif;
}

.footer__about__text {
  font-weight: 400;
}

.footer__about__text .main_text {
  margin-right: 12%;
}

.footer__about__text ul {
  display: flex;
  justify-content: space-between;
  gap: 2%;
}

.footer__about__text li {
  list-style: none;
}

.footer__about__text h4 {
  text-transform: uppercase;
  font-family: "Raleway-Bold";
  font-size: 1.1rem;
  margin-bottom: 3vh;
}

.footer__about__text p {
  font-family: Raleway-Regular;
  font-size: 0.75rem;
  max-width: 250px;
}

.footer__about__text h5 {
  text-transform: uppercase;
  font-family: "Raleway-Bold";
  font-size: 0.9rem;
  margin-bottom: 3vh;
}

.footer__about__text h6 {
  font-family: "Raleway-Bold";
  font-size: 0.85rem;
}

.menu {
  display: none;
}

.main__priorities__colums:is(:hover, :focus) {
  transition: all 0.3s ease 0s;
  transform: scale(1.03);
}

.main__something_new__text img:is(:hover, :focus) {
  transition: all 0.3s ease 0s;
  transform: scale(1.03);
}

.main__something_new__text .text:is(:hover, :focus) {
  transition: all 0.3s ease 0s;
  transform: scale(1.03);
}

.footer__quote_2__text img:is(:hover, :focus) {
  transition: all 0.3s ease 0s;
  transform: scale(1.03);
}

/* Media */
@media screen and (max-width: 1199px) {
  html,
body {
    font-size: 15px;
  }
  .header__content {
    background-size: auto 100%;
  }
  .footer__date__text li::before {
    margin-left: -5px;
  }
  .header__left {
    left: 6%;
  }
  .header__right {
    padding-right: 5%;
  }
  .footer__date__text ul {
    justify-content: space-around;
  }
}
@media screen and (max-width: 991px) {
  html,
body {
    font-size: 14px;
  }
  .main__priorities {
    padding: 10vh 0;
  }
  .main__priorities__content {
    gap: 0;
    overflow-x: auto;
    padding-bottom: 1rem;
    padding-right: 10%;
  }
  .main__priorities__content .container {
    max-width: 100%;
  }
  .main__priorities__colums {
    min-width: 40%;
    margin-left: 10%;
  }
  .main__priorities__content::-webkit-scrollbar {
    height: 3px;
  }
  .main__priorities__content::-webkit-scrollbar-track {
    background-color: transparent;
  }
  .main__priorities__content::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background-color: #111;
  }
}
@media screen and (max-width: 767px) {
  .header__right {
    height: auto;
    width: auto;
    padding-right: 10px;
  }
  .header__content {
    padding: 0;
  }
  .header__left {
    min-width: 0rem;
    width: 0rem;
    height: 0rem;
    left: 0px;
  }
  nav {
    transition: all 0.8s ease 0s;
    display: flex;
    position: fixed;
    z-index: 999;
    align-items: flex-start;
    padding-top: 60px;
    overflow-y: auto;
    background-color: var(--black);
    max-width: 70%;
    height: 105vh;
    top: 0;
    right: 0;
    right: -250%;
    bottom: 0;
    left: 0;
  }
  nav ul {
    text-align: center;
    justify-content: center;
    flex-flow: row wrap;
    align-content: center;
  }
  nav li {
    overflow-y: auto;
    font-size: 2.5rem;
    width: 100%;
  }
  nav .li_home {
    position: static;
    left: auto;
  }
  nav .srick {
    width: 100%;
    height: 4px;
  }
  nav .menu_nav {
    display: none;
  }
  .menu {
    display: flex;
    top: 1vh;
    right: 1%;
    position: fixed;
    display: flex;
    width: 35px;
    height: auto;
    transform: none;
    padding: 5px 5px;
    background-color: var(--purple);
    border-radius: 5px;
    border: 2px solid var(--black);
    gap: 3px;
  }
  .header__right a {
    text-decoration: none;
    background-color: #fff;
